.button-container {
    margin: 25% 50%;
    align-content: center;
}

button {
    background-color: mediumaquamarine;
    border-radius: 5px;
    border: none;
    color: black;
    padding: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }

  .spin {
    height: 800px;
    width: 800px;
    position: absolute;
    left: 50%;
    margin-left: -400px;
    top: 50%;
    margin-top: -400px;
    animation: spin infinite 20s linear;
  }

  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.image-container {
    width: 100%;
    height: 100%;
}

#congrats {
    text-align: center;
    font-size: 5em;
    position: absolute;
    left: 33%;
    top: 15%;
    right: 10em;
    bottom: 10em;
}